@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/css/font-awesome.css);
.c{position:absolute;height:7px;background:white;width:7px;border:1px solid black;border-radius:50%}.c-t{top:-5px;left:-5px}.c-r{top:-5px;right:-5px}.c-l{bottom:-5px;right:-5px}.c-b{bottom:-5px;left:-5px}.backgroundElement{top:-20%;left:-20%;background:orange}.terminal__button{width:15px;height:15px;border-radius:50%;margin:5px}#id1{background-color:#FC6058}#id2{background-color:orange}#id3{background-color:green}#cursor{background:black;-webkit-animation:blink 1.4s infinite;animation:blink 1.4s infinite;width:30px;height:90%}@-webkit-keyframes blink{0%{background:#fff}50%{background:black}100%{background:#fff}}@keyframes blink{0%{background:#fff}50%{background:black}100%{background:#fff}}

@tailwind base;h1{@apply text-2xl}h2{@apply text-xl}h3{@apply text-lg}a{@apply text-blue-600 underline}@tailwind components;@tailwind utilities;.fa-linkedin{padding:10px 14px;transition:.5s;background:#0a66c2}.fa-github{padding:10px 14px;transition:.5s}

